import React from "react"
import { Link } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"

const Word = () => (
  <Layout>
    <SEO title="Vad betyder svensk spellicens?" />
    <div className="content-header">
      <h1>Vad betyder svensk spellicens?</h1>
      <p>Spellicens som gör det möjligt för spelbolag att aktivt erbjuda spel för svenskar.</p>
      <Link to="/" className="back-button">Tillbaka till startsidan</Link>
    </div>
  </Layout>
)

export default Word
